@font-face {
  font-family: "Lato";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Lato-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Oswald";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Oswald-Regular.woff2") format("woff2");
}
img {
  max-width: 100%;
  height: auto;
}

body {
  margin: 0 auto;
  min-height: 100%;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  font-size: 18px;
  line-height: 30px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  font-family: inherit;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 9px;
  background-color: #68b738;
  border: 0;
  outline: 0;
  cursor: pointer;
}

.container {
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 768px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
.page {
  height: 100%;
  font-size: 18px;
  font-family: "Oswald", sans-serif;
  line-height: 27px;
}

.page__body {
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
  color: #000000;
}

.main-header {
  display: flex;
  margin-top: 15px;
  justify-content: space-between;
  background-color: #ffffff;
  color: #000000;
}

.main-header__wrapper {
  padding-left: 30px;
  padding-right: 30px;
  justify-content: space-between;
  width: 100%;
}

.main-header__logo {
  display: flex;
  height: 65px;
  text-align: center;
}

.main-header__logo-title {
  margin-left: 20px;
}

.main-header__logo-picture__tablet {
  display: none;
}

.main-nav {
  position: relative;
  min-height: 200px;
  background-color: transparent;
}

.main-nav__toggle {
  top: 0;
  left: auto;
  z-index: 2;
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: transparent;
  color: #000000;
}

.main-header__top {
  display: flex;
  justify-content: space-between;
  height: 66px;
}

.main-nav__list-link {
  display: inline-block;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  color: #000000;
  padding-top: 22px;
  padding-bottom: 23px;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
}

.main-nav__list-item {
  margin-left: -20px;
  margin-right: -20px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
}

.main-nav__list-item--active .main-nav__list-link {
  position: relative;
}

.main-nav__list-item--active .main-nav__list-link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 14px;
  width: 100%;
  height: 2px;
  background-color: #68b738;
}

@media (min-width: 768px) {
  .main-header {
    display: flex;
    margin: 0 auto;
    height: 50px;
    margin-top: 25px;
  }
  .main-header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .main-nav__list-item {
    border: none;
    padding: 10px 33px;
  }
  .main-header__logo-picture {
    width: 60px;
    height: 50px;
  }
  .main-nav__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  .main-header__logo-picture__tablet {
    display: block;
  }
  .main-nav {
    min-height: 50px;
    width: 66%;
  }
  .main-header__top {
    justify-content: flex-start;
    width: 27.11%;
    padding: 0;
  }
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    justify-content: flex-end;
    width: 100%;
    margin-left: auto;
  }
}
